import React, { useEffect, useState } from "react";

function IframeComponent(props) {
  useEffect(() => {
    document.getElementById("helperForm").submit();
  });

  return (
    <div>
      <form
        id="helperForm"
        target="embeddedGoodDataIframe"
        method="post"
        action="https://lamdata.na.gooddata.com/gdc/account/customerlogin"
      >
        <input
          id="targetUrl"
          type="hidden"
          name="targetUrl"
          value={props.targetUrl}
        />
        <input
          id="ssoProvider"
          type="hidden"
          name="ssoProvider"
          value={props.ssoProvider}
        />
        <input
          id="encryptedClaims"
          type="hidden"
          name="encryptedClaims"
          value={props.encryptedClaims}
        />
      </form>

      <iframe
        name="embeddedGoodDataIframe"
        style={{ width: "100%", height: "100%", minHeight: "85vh" }}
        target="_parent"
      />
    </div>
  );
}

export default IframeComponent;
