import React, { useEffect, useState } from "react";
import MUIDataTable from "mui-datatables";
import { useHistory } from "react-router-dom";
import Constants from "../utils/constants";
import Service from "../utils/service";
import LoadingPanel from "../components/loadingPanel";
import { Button } from "@material-ui/core";
import { Link } from "react-router-dom";
import { findAllByDisplayValue } from "@testing-library/react";

function Admin(props) {
  const [rows, setRows] = useState([]);
  const [headers, setHeaders] = useState([]);
  const [status, setStatus] = useState(Constants.LOADING);
  const [panelData, setPanelData] = useState({});
  let history = useHistory();

  useEffect(() => {
    Service.axiosApi
      .get(Service.apis.getUsers)
      .then(response => {
        setStatus(Constants.SUCCESS);

        // updating the rows
        setRows(prevState => {
          return prevState.concat(response.data);
        });

        var one = response.data[0];

        var tempHeaders = [
          { label: "First Name", name: "firstName" },
          { label: "Last Name", name: "lastName" },
          { label: "Email", name: "email" },
          { label: "Position", name: "position" }
        ];

        // adding action to table column
        tempHeaders.push({
          name: "Actions",
          options: {
            filter: false,
            sort: false,
            empty: true,
            print: false,
            customBodyRender: (value, tableMeta, updateValue) => {
              return (
                <div style={{ display: "flex", flexDirection: "row" }}>
                  <button onClick={e => handleClick(e, tableMeta, "delete")}>
                    Delete
                  </button>
                  <button
                    style={{ marginLeft: "15px" }}
                    onClick={e => handleClick(e, tableMeta, "edit")}
                  >
                    edit
                  </button>
                </div>
              );
            }
          }
        });

        setHeaders(tempHeaders);
      })
      .catch(error => {
        // setPanelData({
        //   error: error,
        //   text: "Loading candidates failed"
        // });
        // setStatus(Constants.FAIL);
        // alert(error);
      });
  }, []);

  var handleClick = (e, data, type) => {
    // eslint-disable-next-line default-case
    switch (type) {
      case "delete":
        // alert(type + " : " + data.rowData[0]);
        deleteUser(data.rowData[2]);
        break;
      case "permission":
        alert(type + " : " + data.rowData[0]);
        break;
      case "edit":
        history.push("/admin/updateUser", data.rowData[2]);
        break;
      case "showReport":
        // history.push("/rdashboard");
        var value = data.rowData[1];
        var prjurl = value
          .substring(0, value.indexOf("/obj"))
          .replace("/md", "/projects");

        var targetUrl =
          "/reportWidget.html#project=" +
          prjurl +
          "&report=" +
          value +
          "&title=yes";

        history.push("/rdashboard", { [Constants.IFRAME_URL]: targetUrl });

        break;
    }
  };

  function deleteUser(userName) {
    setStatus(Constants.LOADING);

    Service.axiosApi
      .delete(Service.apis.deleteuser, { username: userName })
      .then(res => {
        setStatus(Constants.SUCCESS);
      })
      .catch(err => {
        setStatus(Constants.SUCCESS);
      });
  }

  const options = {
    filter: true,
    filterType: "dropdown",
    page: 2,
    selectableRows: false,
    responsive: "scroll",
    onColumnSortChange: (changedColumn, direction) =>
      console.log("changedColumn: ", changedColumn, "direction: ", direction),
    onChangeRowsPerPage: numberOfRows =>
      console.log("numberOfRows: ", numberOfRows),
    onChangePage: currentPage => console.log("currentPage: ", currentPage)
  };

  return (
    <LoadingPanel status={status} data={panelData}>
      <div style={{ margin: "32px" }}>
        <div style={{ textAlign: "right", marginBottom: "32px" }}>
          <Link style={{ textDecoration: "none" }} to="/admin/addUser">
            <Button variant="contained" color="secondary" disableElevation>
              Add User
            </Button>
          </Link>
        </div>

        <MUIDataTable
          title={"User List"}
          data={rows}
          columns={headers}
          options={options}
        />
      </div>
    </LoadingPanel>
  );
}

export default Admin;
