import React, { useEffect, useState } from "react";
import MUIDataTable from "mui-datatables";
import { useHistory } from "react-router-dom";
import Constants from "../utils/constants";
import Service from "../utils/service";
import LoadingPanel from "./../components/loadingPanel";

function Reports(props) {
  const [rows, setRows] = useState([]);
  const [headers, setHeaders] = useState([]);
  const [status, setStatus] = useState(Constants.LOADING);
  const [panelData, setPanelData] = useState({});
  let history = useHistory();

  useEffect(() => {
    Service.axiosApi
      .post(Service.apis.getReports, {
        projectId: sessionStorage.getItem(Constants.SELECTED_PROJECT)
      })
      .then(response => {
        setStatus(Constants.SUCCESS);

        // updating the rows
        setRows(prevState => {
          return prevState.concat(response.data);
        });

        var one = response.data[0];

        var tempHeaders = [
          {
            label: "Report Name",
            name: "title",
            options: {
              customBodyRender: (value, tableMeta, updateValue) => {
                return (
                  <div style={{ display: "flex", flexDirection: "row" }}>
                    <div
                      style={{
                        textDecoration: "underline",
                        color: "blue",
                        cursor: "pointer"
                      }}
                      onClick={e => handleClick(e, tableMeta, "showReport")}
                    >
                      {value}
                    </div>
                  </div>
                );
              }
            }
          },
          {
            label: "Link",
            name: "link",
            options: {
              display: false
            }
          },
          { label: "Author", name: "author" },
          { label: "Created", name: "created" },
          { label: "Updated", name: "updated" },
          { label: "Tags", name: "tags" },
          { label: "Summary", name: "summary" }
        ];

        // adding action to table column
        tempHeaders.push({
          name: "Actions",
          options: {
            filter: false,
            sort: false,
            empty: true,
            customBodyRender: (value, tableMeta, updateValue) => {
              return (
                <div style={{ display: "flex", flexDirection: "row" }}>
                  <button onClick={e => handleClick(e, tableMeta, "delete")}>
                    Delete
                  </button>
                  <button
                    style={{ marginLeft: "15px" }}
                    onClick={e => handleClick(e, tableMeta, "permission")}
                  >
                    permission
                  </button>
                </div>
              );
            }
          }
        });

        setHeaders(tempHeaders);
      })
      .catch(error => {
        setPanelData({
          error: error,
          text: "Loading candidates failed"
        });
        setStatus(Constants.FAIL);
        // alert(error);
      });
  }, []);

  var handleClick = (e, data, type) => {
    // eslint-disable-next-line default-case
    switch (type) {
      case "delete":
        alert(type + " : " + data.rowData[0]);
        break;
      case "permission":
        alert(type + " : " + data.rowData[0]);
        break;
      case "showReport":
        // history.push("/rdashboard");
        var value = data.rowData[1];
        var prjurl = value
          .substring(0, value.indexOf("/obj"))
          .replace("/md", "/projects");

        var targetUrl =
          "/reportWidget.html#project=" +
          prjurl +
          "&report=" +
          value +
          "&title=yes";

        history.push("/rdashboard", { [Constants.IFRAME_URL]: targetUrl });

        break;
    }
  };

  const options = {
    filter: true,
    filterType: "dropdown",
    page: 2,
    selectableRows: false,
    responsive: "scroll",
    onColumnSortChange: (changedColumn, direction) =>
      console.log("changedColumn: ", changedColumn, "direction: ", direction),
    onChangeRowsPerPage: numberOfRows =>
      console.log("numberOfRows: ", numberOfRows),
    onChangePage: currentPage => console.log("currentPage: ", currentPage)
  };

  return (
    <LoadingPanel status={status} data={panelData}>
      <div style={{ margin: "32px" }}>
        <MUIDataTable
          title={"Reports"}
          data={rows}
          columns={headers}
          options={options}
        />
      </div>
    </LoadingPanel>
  );
}

export default Reports;
