import React from "react";
import errorImg from "../../images/error_img.svg";

const SomethingWrong = props => {
  return (
    <div
      style={{
        height: "88vh",
        display: "flex",
        alignItems: "center",
        flexDirection: "column",
        justifyContent: "center"
      }}
    >
      <img src={errorImg} style={{ height: 250, marginBottom: 32 }} />
      <h1>{props.data.text}</h1>
      <span>{props.data.error.message}</span>
    </div>
  );
};

export default SomethingWrong;
